#sideNavbarEnclosingDiv{
  text-align: center;
  height: 100%;
  width: 200px;
  position: fixed;
  transform: translate(0%,60px);
  z-index: 1;
  top: 0;
  left: 0;
  overflow-x: hidden;
  padding-top: 3%;
}
#navbarAccountsButton{
  padding-bottom: 20%;
}
#sideNavbarEnclosingDiv a{
    font-size: 25px;
    display: block;
    color: #818181;
}
#sideNavbarEnclosingDiv a:hover {
  color: olive;
}
